<template>
  <tab :line-width=2 active-color='#d9261a' :index.sync="index" class="jq-test">
    <tab-item class="vux-center" :selected="demo2 === item" v-for="item in list2" @click="demo2 = item">{{item}}</tab-item>
  </tab>
  <swiper :index.sync="index" :show-dots="false" height="10rem" class="clearfix jq-te">
    <swiper-item class="jq-bu">
      <div class="jq-so">
      <!--<div class="tab-swiper vux-center">-->
        <group class="jq-mt">
          <popup-picker :title="title" :data="listTime" :value.sync="time"></popup-picker>
        </group>
      <!--</div>-->
        <group class="jq-mt">
        <div class="tab-swiper vux-center marginT">
          <h1 class="keyMsg">销售额汇总</h1>
          <!--<countup :start-val="1" :end-val="8888888 height="10rem" :duration="4" class="contas"></countup>-->
        </div>
        <div class="tab-swiper vux-center marginT">
          <div class="contas">{{dataSALES}}亿元</div>
          <!--<countup :start-val="1" :end-val=dataSALES :duration="4" class="contas"></countup>-->
        </div>
        </group>
        <group class="jq-mt">
        <div class="tab-swiper vux-center paddingT">
          <flexbox>
          <flexbox-item></flexbox-item>
          <flexbox-item>
            <div style='width:2rem;height:2rem;'>
              <circle :percent="dataRATE" :stroke-width="6" :trail-width="6" :stroke-color="strokeColor" trail-color="#ececec">
                <span class="circleTit jqTI">达成率 {{dataRATE}}%</span>
                <span :style="{color: strokeColor2}" class="circleText centerTit"><i class={{flag}}></i>{{dataReduce}}%</span>
              </circle>
            </div>
          </flexbox-item>
          <flexbox-item></flexbox-item>
        </flexbox>
        </div>
        </group>
        <div class="tab-swiper vux-center jq-mt">
          <div class="pan" v-ebarline="barLineData"></div>
        </div>
      </div>
    </swiper-item >
    <swiper-item class="jq-bu">
      <div class="jq-so">
        <div class="tab-swiper vux-center">
          <div class="pan" v-epie="pieData"></div>
        </div>
        <div class="tab-swiper vux-center">
          <flexbox class="jq-mt">
            <flexbox-item>
              <group>
                <popup-picker :title="title2" :data="listBus" :value.sync="dataOne"></popup-picker>
              </group>
            </flexbox-item>
            <flexbox-item>
              <group>
                <popup-picker :title="title" :data="listTime" :value.sync="time" ></popup-picker>
              </group>
            </flexbox-item>
          </flexbox>
        </div>
        <div class="tab-swiper vux-center jq-mt">
          <!--<group class="jq-mt">-->
          <div class="pan" v-ebarline="barLineDataS"></div>
          <!--</group>-->
        </div>
      </div>
    </swiper-item>
  </swiper>
</template>

<script>
  import Group from '*/group'
  import {Tab,TabItem} from '*/tab'
  import Swiper from '*/swiper'
  import SwiperItem from '*/swiper-item'
  import Cell from '*/cell'
  import Scroller from '*/scroller'
  import Countup from '*/countup'
  import Divider from '*/divider'
  import Circle from '*/circle'
  import Range from '*/range'
  import Icon from '*/icon'
  import {Flexbox,FlexboxItem} from '*/flexbox'
  import PopupPicker from '*/popup-picker'

  import { getList } from '../../../untils/api'
  const list = () => ['销售汇总', '事业部明细']
  export default {
  components: {
    Group,
    Tab,
    TabItem,
    Swiper,
    SwiperItem,
    Countup,
    Divider,
    FlexboxItem,
    Flexbox,
    Circle,
    Range,
    Icon,
    PopupPicker,
    //Sticky
  },
  data () {
    return {
      list2: list(),
      demo2: '销售汇总',
      index: 0,
      //percent1: 10,
      //percent2: 50,
      strokeColor2: '#5CB85C',
      strokeColor: '#5CB85C',
      barLineData:{},
      pieData:{},
      barLineDataS:{},
      title: '时间维度',
      time:formatData.defaultTimeYear,
      listTime: formatData.dateListYear,
      title2: '事业部',
      dataOne:['FPI'],
      listBus: [formatData.busList],
      dataSALES:'',
      dataRATE:10,
      dataReduce:10,
      flag:"",
    }
  },
  ready(){
    //this.getDataLive();

    document.title = '销售汇总';
    this.getData();
    //this.update();
  },
  methods: {
    async getDataLive() {
        commit('UPDATE_LOADING', true)
      let id ='APP10119';
      let dataTit = await getList(id,{});
      //console.log(dataTit);
      if(dataTit.success){

        const TableList = dataTit.data;
        this.barLineData = {};
        this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
          commit('UPDATE_LOADING', false)
      }else{
          commit('UPDATE_LOADING', false)
        console.log(dataTit.message);
      }
    },
    async getPieData() {
        commit('UPDATE_LOADING', true)
      let id ='APP10120';
      let dataT = await getList(id,{});
      //console.log(dataT);
      if(dataT.success){

        const TableList = dataT.data;
        this.pieData = {};
        this.pieData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
          commit('UPDATE_LOADING', false)
      }else{
          commit('UPDATE_LOADING', false)
        console.log(dataTit.message);
      }
    },
    async getDataS() {
        commit('UPDATE_LOADING', true)
      let id='';
        if(this.dataOne=='FPI'){
          id="APP10121";
        }else if(this.dataOne=='SMD'){
         id="APP10122";
        }else if(this.dataOne=='ADD'){
         id="APP10123";
        }
      let dataTit = await getList(id,{});
      //console.log(dataTit);
        if(dataTit.success){

          //表头：
          //表格:
          const TableList = dataTit.data;
          this.barLineDataS = {};
          this.barLineDataS.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
            commit('UPDATE_LOADING', false)
        }else{
            commit('UPDATE_LOADING', false)
          console.log(dataTit.message);
        }
    },
      async getData() {
        commit('UPDATE_LOADING', true)
      let id ='APP10134';
      let dataTit = await getList(id,{});
      console.log(dataTit);
      if(dataTit.success){
        //销售额汇总
        this.dataSALES=dataTit.data.result.list[0].SALES_
        //this.dataSALES=99999
        //销售达成率
        this.dataRATE=dataTit.data.result.list[0].RATE_*100
        //达成率差额
        //let data_=(dataTit.data.result.list[0].RATE_*100-dataTit.data.result.list[0].RATE_OLD*100)
        //console.log(data_);
        this.dataReduce=(dataTit.data.result.list[0].RATE_*100-dataTit.data.result.list[0].RATE_OLD*100).toFixed(2);
        //console.log(this.dataSALES);
        //颜色样式
        const colorMap = ['#D9534F', '#F0AD4E', '#5CB85C'];
        const flag=["fa fa-arrow-up","fa fa-arrow-down"];

        if(this.dataReduce<0){
          this.strokeColor2 = colorMap[0];
          this.flag=flag[1]
        }else{
          this.strokeColor2 = colorMap[2];
          this.flag=flag[0]
        }

        if(this.dataRATE>90||this.dataRATE===90){
          this.strokeColor=colorMap[2];
        }else if(this.dataRATE<90&&this.dataRATE>30||this.dataRATE===30){
          this.strokeColor=colorMap[1];
        }else if(this.dataRATE<30){
          this.strokeColor=colorMap[1];
        }


        commit('UPDATE_LOADING', false);
      }else{
        commit('UPDATE_LOADING', false)
      }
      /**if(dataTit.success){
        this.msg=dataTit.data.workDescribe;
        const TableList = dataTit.data;
        this.barLineData = {};
        this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
          commit('UPDATE_LOADING', false)
      }else{
          commit('UPDATE_LOADING', false)
        console.log(dataTit.message);
      }*/
    }
  },
  watch: {
   dataOne (newdataOne, olddataOne) {
    this.getDataS();
    },
    time (newIndex, oldIndex) {
       formatData.setDateTime(newIndex);
    }
  },
  created: function () {
    this.barLineData = {};
    this.barLineData.dataset = [];
    this.getDataLive();

    this.pieData = {};
    this.pieData.dataset = [];
    this.getPieData()

    this.barLineDataS = {};
    this.barLineDataS.dataset = [];
    this.getDataS();

  }
}
</script>



